@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
	text-align: center;
}

* {
	margin: 0;
	padding: 0;
}

body {
	font-family: Arial, Helvetica, sans-serif;
	line-height: 1.4;
}

a {
	text-decoration: none;
	color: #333;
}

.NavBar{height:50px;width:100%;position:-webkit-sticky;position:sticky;top:0px;background-color:white;border:2px solid black;display:flex;flex-wrap:unset;justify-content:center}.NavItem{display:inline-block;flex:1 1 150px;border:2px solid red;margin:auto;font-size:2em}.NavHeader{display:inline-block;justify-content:center;flex:0 1 200px;border:2px solid blue;background-position:center;background-repeat:no-repeat;background-size:auto 100%}

.FooterContainer{text-align:left;background-color:#999999;font-size:1em;padding:5px 20px 5px 20px;width:100%;height:30px}


.About{display:grid;place-items:center;width:100%;height:100%}.ScrambledText{font-size:2em}

body{padding:0;margin:0;font-family:"Montserrat", sans-serif}h1{font-size:60px;text-align:center}.timeline{position:relative;margin:50px auto;padding:40px 0;width:1000px;box-sizing:border-box}.timeline:before{content:"";position:absolute;left:50%;width:2px;height:100%;background:#c5c5c5}.timeline ul{padding:0;margin:0}.timeline ul li{list-style:none;position:relative;width:50%;padding:20px 40px;box-sizing:border-box}.timeline ul li:nth-child(odd){float:left;text-align:right;clear:both}.timeline ul li:nth-child(even){float:right;text-align:left;clear:both}.content{padding-bottom:20px}.timeline ul li:nth-child(odd):before{content:"";position:absolute;width:10px;height:10px;top:24px;right:-6px;background:#e92163;border-radius:50%;box-shadow:0 0 0 3px rgba(233,33,99,0.2)}.timeline ul li:nth-child(even):before{content:"";position:absolute;width:10px;height:10px;top:24px;left:-4px;background:#e92163;border-radius:50%;box-shadow:0 0 0 3px rgba(233,33,99,0.2)}.timeline ul li h3{padding:0;margin:0;color:#e92163;font-weight:600}.timeline ul li p{margin:10px 0 0;padding:0}.timeline ul li .time h4{margin:0;padding:0;font-size:14px}.timeline ul li:nth-child(odd) .time{position:absolute;top:12px;right:-165px;margin:0;padding:8px 16px;background:#e92163;color:#fff;border-radius:18px;box-shadow:0 0 0 3px rgba(233,33,99,0.3)}.timeline ul li:nth-child(even) .time{position:absolute;top:12px;left:-165px;margin:0;padding:8px 16px;background:#e92163;color:#fff;border-radius:18px;box-shadow:0 0 0 3px rgba(233,33,99,0.3)}@media (max-width: 1000px){.timeline{width:100%}}@media (max-width: 767px){.timeline{width:100%;padding-bottom:0}h1{font-size:40px;text-align:center}.timeline:before{left:20px;height:100%}.timeline ul li:nth-child(odd),.timeline ul li:nth-child(even){width:100%;text-align:left;padding-left:50px;padding-bottom:50px}.timeline ul li:nth-child(odd):before,.timeline ul li:nth-child(even):before{top:-18px;left:16px}.timeline ul li:nth-child(odd) .time,.timeline ul li:nth-child(even) .time{top:-30px;left:50px;right:inherit}}



.Theme{height:100%;width:100%;display:grid;grid-template-columns:auto 1fr auto;place-items:center}.ThemeElement{border:2px solid blue;display:grid;grid-template-rows:auto auto auto;height:100%;width:100%;min-width:200px;place-items:center}.ThemeSubElement{border:2px solid greenyellow;height:100%;width:100%;display:grid;place-items:center;opacity:0;animation:fadeIn 1s 1;animation-fill-mode:forwards;font-size:2em}.ThemeSubElement:nth-child(2n){animation-delay:1s}.ThemeSubElement:nth-child(3n){animation-delay:2s}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}.SpecialThemeSubElement1{position:relative;left:50%}.SpecialThemeSubElement2{position:relative;right:50%}.ThemeMain{border:2px solid red;border-radius:50%;height:95%;width:80%}@media only screen and (max-width: 700px){.Theme{grid-template-columns:auto;grid-template-rows:auto 1fr auto}.ThemeSubElement{left:0px;text-align:left;place-items:initial;padding-left:10%}.SpecialThemeSubElement1{align-items:left;left:0px;text-align:left;place-items:end;padding-right:10%}.SpecialThemeSubElement2{align-items:right;left:0px;text-align:right;place-items:end;padding-right:10%}.ThemeMain{height:100%;width:100%}}

.MainContainer{display:grid;grid-template-rows:auto 1fr auto;border:2px solid skyblue;min-height:100vh}

